import { Component, Input, OnInit } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownIndividualCifNoExposed } from './ps-dropdown-individual-cif-no.component.interface';

@Component({
  selector: 'ps-dropdown-individual-cif-no',
  templateUrl: './ps-dropdown-individual-cif-no.component.html',
  styleUrls: ['./ps-dropdown-individual-cif-no.component.scss'],
})
export class PsDropdownIndividualCifNoComponent extends PsSelectDropdownComponent implements OnInit {
  @Input() options: IOptionsPsDropdownIndividualCifNoExposed;
  defaultSelectOptions: IOptionsPsSelectDropdown = {
    labelKey: 'individual_cif_no_key',
    placeHolder: 'select_individual_cif_no_key'
  };

  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }
  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options);
    this.returnIndividualCifNo();
  }

  onChange(value: IchangeValues) {
    this.onPsChange.emit(value);
  }

  private async returnIndividualCifNo() {

    const result = await this.omniPull.returnCIFList({}).catch((error) => {
      this.commonProv.logger.error(
        "Error: While fetching returnCIFList in cif-list-report page :",
        error
      );
    });
    this.defaultSelectOptions.listOfOptions = [];
    if (result && result.gridModel && result.gridModel.length > 0) {
      for (const iterator of result.gridModel) {
        const documents: any = {
          itemValue: iterator.userCifNo,
          description: iterator.userCifNo + '-' + iterator.firstName,
          selectedObj: iterator
        };
        this.defaultSelectOptions.listOfOptions.push(documents);
      }
    }
  }

}
